.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html{
  scroll-behavior: unset;
    scroll-behavior: smooth;
    user-select: none;
}

.swiper-pagination-bullet-active
{
    background-color: rgb(75,85,99) !important;
}

.center .swiper-pagination-bullet-active
{
    background-color: rgb(245, 245, 245) !important;
}

a.box-hover:hover > div{
    height: 100%;
    
}
.loop{
    background-position: left;
    background-size: 200%;
    animation: anim 5s infinite alternate;
}

@keyframes anim{
    0%{
        background-position: left;
    }
    100%{
        background-position: right;
    }
}

.loader {
    position: absolute;
    top: calc(50% - 32px);
    left: calc(50% - 32px);
    width: 64px;
    height: 64px;
    border-radius: 50%;
    perspective: 800px;
  }
  
  .inner {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    border-radius: 50%;  
  }
  
  .inner.one {
    left: 0%;
    top: 0%;
    animation: rotate-one 1s linear infinite;
    border-bottom: 3px solid #EFEFFA;
  }
  
  .inner.two {
    right: 0%;
    top: 0%;
    animation: rotate-two 1s linear infinite;
    border-right: 3px solid #EFEFFA;
  }
  
  .inner.three {
    right: 0%;
    bottom: 0%;
    animation: rotate-three 1s linear infinite;
    border-top: 3px solid #EFEFFA;
  }
  
  @keyframes rotate-one {
    0% {
      transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
    }
    100% {
      transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
    }
  }
  
  @keyframes rotate-two {
    0% {
      transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
    }
    100% {
      transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
    }
  }
  
  
  
  @keyframes rotate-three {
    0% {
      transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
    }
    100% {
      transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
    }
  }

.menu-down:hover > div{
display: flex;
opacity: 1;
transition: all 1s;
}

#search:focus + button {
  color: rgb(107 114 128);
}

#nav-menu{
  backdrop-filter: blur(10px);
}

.bd{
  backdrop-filter: blur(5px);
}
input[type='radio']:checked + div{
  border:4px solid rgba(91, 178, 250, 0.863);
}

.detail::after{
  content: ">";
    color: black;
    position: absolute;
    left: 110%;
    top: 50%;
    margin-bottom: 1rem;
    transform: translateY(-50%);
    
}

.padding-swiper{
  padding: 1rem 1rem !important;
}

.padding-swiper .swiper-pagination-bullet-active{
background-color: white !important;
}

.w-inher{
  width: inherit;
}

.anim-right{
  background:linear-gradient(90deg, white 0%, white 35%,#f8f8f8 45%,#f8f8f8 55%,white 65%,white 100%);
  background-size: 400%;
  animation: torightanim 1s ease-in-out infinite;
}

@keyframes torightanim {
  0%{
    background-position: 0, 0;
  }
  100%{
    background-position: 100%, 0;
  }
}

.anim-top{
  background:linear-gradient(90deg, #f8f8f8 0%, #f8f8f8 35%,white 45%,white 55%,#f8f8f8 65%,#f8f8f8 100%);
  background-size: 400%;
  animation: torightanim 1s ease-in-out infinite;
}

@keyframes torightanim {
  0%{
    background-position: 0, 0;
  }
  100%{
    background-position: 100%, 0;
  }
}

